:root {
  --white: white;
  --black: #2c2c2c;
  --dark-text: #35363f;
  --gray-dark: #707070;
  --gray-med: #959595;
  --gray-lt: #c2c0c0;
  --gray-lightest: #fbfbfb;
  --dusk: #435363;
  --fog: #607774;
  --storm: #4f869f;
  --teal: #85c4ca;
  --gold: #ffd91f;
  --green: #4d9b5e;
  --red: #b31b1b;
  --gray: #c2c0c0;
  --gray-light: #d9d9d9;
  --near-white: #fbfbfb;
  --font-sans: "canada-type-gibson", sans-serif;
  --font-serif: "freight-micro-pro", verdana;
  --font-curly: "Alisha";
  --base-font-size: 20px;
  --medium-font-size: 20px;
  --small-font-size: 16px;
  --sidebar-font-size: 20px;
  --main-font-size: 28px;
  --font-size--huge: 60px;
  --light: 300;
  --regular: 400;
  --semi: 600;
  --heavy: 800;
  --line-height: 1.4;
  --border-width: 1px;
  --border-color: var(--gray-light);
  --border-radius: 4px;
  --space: 1rem;
  --mainGutter: var(--space);
  --auto-grid-min-size: calc(10 * var(--space));
  --grid-gap: var(--mainGutter);
  --base-grid-column: 84px;
  --component-bottom-margin: calc(1 * var(--space)); }
  @media (min-width: 1224px) {
    :root {
      --base-font-size: 24px; } }
  @media (min-width: 782px) {
    :root {
      --space: 1.5rem; } }
  @media (min-width: 480px) {
    :root {
      --mainGutter: var(--space); } }
  @media (min-width: 782px) {
    :root {
      --mainGutter: calc(2 * var(--space)); } }

/* -------------------------

Title: _base.scss
Descr: sets HTML and Body styles

--------------------------- */
html {
  font-size: var(--base-font-size); }

body {
  font-family: var(--font-sans);
  font-size: var(--base-font-size);
  color: var(--#000000);
  font-weight: var(--regular);
  line-height: var(--line-height);
  overflow-x: hidden;
  color: var(--#008000); }

/* -------------------------

Title: <main> layouts.
Descr: 
    main content is the <main> tag. It is where the main components are. It is a child of the .container.

--------------------------- */
.main {
  max-width: 1432px;
  margin: 0 auto;
  display: grid;
  grid-gap: 2rem;
  grid-row-gap: 0;
  padding: 0 1rem; }
  @media (min-width: 782px) {
    .main {
      grid-template-columns: repeat(12, calc(8.59% - 2rem)); } }

.basic > div,
.landingPage > div {
  grid-column: 1 / -1; }

.basicSidebar {
  grid-template-rows: auto auto 1fr;
  grid-template-areas: "nav" "title" "content" "sidebar"; }
  .basicSidebar .siteNav {
    grid-area: nav; }
  .basicSidebar .title {
    grid-area: title; }
  .basicSidebar .sidebar {
    grid-area: sidebar; }
  .basicSidebar .content {
    grid-area: content; }
  .basicSidebar aside {
    grid-area: aside; }
  @media (min-width: 782px) {
    .basicSidebar {
      grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content content content" "sidebar sidebar sidebar sidebar content content content content content content content content"; } }
  @media (min-width: 782px) {
    .basicSidebar--has-aside {
      grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content aside aside" "sidebar sidebar sidebar sidebar content content content content content content aside aside"; } }

/* -------------------------

Title: container layouts.
Descr: 
    this is the container that spans from the <header> to <footer>. It establishes columns for sidebars etc. .mainContent(--modifier) is a child of .container.

    - --has-aside and has--sidebar are the base models. Basic page, and TOC both use them

    -- article and --hasPano are for articles, more complicated grids

--------------------------- */
.container {
  grid-column: 2;
  display: grid;
  align-items: flex-start;
  align-content: flex-start; }
  @media (min-width: 1224px) {
    .container--has-aside {
      grid-gap: 2rem;
      grid-template-columns: 1fr minmax(200px, 300px);
      grid-template-areas: "title title" "components aside"; }
      .container--has-aside .title {
        grid-area: title; }
      .container--has-aside .components {
        grid-area: components; }
      .container--has-aside aside {
        grid-area: aside; } }
  .container--has-sidebar .toc-menu, .container--has-sidebar .sidebar-nav {
    grid-area: menu; }
  .container--has-sidebar .sidebar {
    grid-area: sidebar; }
  .container--has-sidebar main {
    grid-area: main; }
  @media (min-width: 1640px) {
    .container--has-sidebar {
      grid-gap: calc( 3 * var(--space));
      grid-row-gap: var(--space); } }

.page-title {
  grid-area: page-title;
  display: block; }

.main-content {
  grid-area: content; }

/* -------------------------

Title: Swiss Army 
Descr: Where all little helper classes that don't go on one specific component css will live. They all must be applicable to multiple elements.

--------------------------- */
.hidden {
  display: none; }

.sr-only {
  display: block;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  white-space: nowrap;
  position: absolute; }

/* 
  Base styles for all figures
  - basic figure
  - figure based on different view modes
  - extra class based on image style to set max widths
*/
figure {
  margin: 1rem 0;
  background: var(--gray-lightest);
  width: 100%;
  border-radius: var(--border-radius); }
  figure.figure--pano {
    margin: 0; }
  figure.figure--thumbnail {
    background: none; }

.pageComponent--ftb .figure--6_4_newsletter {
  max-width: 600px; }

.pageComponent--ftb .figure--5_4 {
  max-width: 520px; }

.pageComponent--ftb .figure--4_5 {
  max-width: 300px; }

figcaption {
  font-weight: var(--light);
  font-size: var(--medium-font-size);
  padding: 1rem; }

/* -------------------------

Title: image
Descr: 
    Most basic universal image styles

--------------------------- */
img,
picture {
  display: block;
  height: auto;
  max-width: 100%; }

/* -------------------------

Title: campfire
Descr: 
    sets up main campfire, three columns, and responsive breakdown

--------------------------- */
.campfire {
  display: grid;
  grid-gap: var(--space);
  grid-row-gap: 0;
  grid-template-columns: 1fr;
  margin-bottom: var(--component-bottom-margin); }
  .campfire > div {
    display: flex;
    flex-direction: column; }
  @media (min-width: 782px) {
    .campfire {
      grid-template-columns: repeat(3, 1fr);
      grid-template-areas: "one one two" "three three three"; }
      .campfire__articles:nth-child(2n+2) {
        grid-area: two; }
      .campfire__articles:nth-child(3n+3) {
        grid-area: three;
        display: grid;
        grid-gap: var(--space);
        grid-row-gap: 0;
        grid-template-columns: repeat(auto-fill, minmax(var(--auto-grid-min-size), 1fr));
        grid-auto-flow: dense; }
      .campfire__feature {
        grid-area: one; } }
  @media (min-width: 1224px) {
    .campfire {
      grid-template-columns: repeat(3, 1fr) repeat(6, 0.75fr) repeat(3, 1fr);
      grid-template-areas: "one one one two two two two two two three three three"; }
      .campfire__articles:nth-child(2n+2) {
        grid-area: one; }
      .campfire__articles:nth-child(3n+3) {
        grid-area: three; }
      .campfire__feature {
        grid-area: two; } }
  @media (min-width: 1432px) {
    .campfire {
      gird-template-columns: repeat(12, 1fr); } }

.campfire__support {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  background: var(--gray-lightest);
  padding: var(--space);
  margin-bottom: 2rem; }
  @media (min-width: 1432px) {
    .campfire__support p {
      font-size: 1.25rem; } }
  .campfire__support h2 {
    font-weight: var(--heavy);
    margin-top: .5rem; }

/* -------------------------

Title: Cards
Descr: Sets grid default card styles
Variations: 
  card--small
  card--article
  card--feature
  card--stat

--------------------------- */
.card {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  margin-bottom: var(--space); }

.card__title {
  font-size: 1rem;
  line-height: 1.3;
  font-weight: var(--semi); }
  .card__title-link {
    color: var(--#000000); }

.card__label {
  margin-bottom: 0;
  color: var(--gray-dark); }

.card__body {
  margin: 0.7em 0 1em; }

.card--small {
  display: grid;
  grid-gap: var(--space);
  grid-template-columns: var(--base-grid-column) 1fr; }
  .card--small .card__title {
    font-family: var(--font-sans);
    font-size: var(--medium-font-size);
    color: var(--#000000);
    font-weight: var(--regular);
    line-height: 1.4; }
  .card--small .card__label,
  .card--small .card__byline {
    display: none; }
  .card--small .card__date.card__date {
    text-align: left; }

[data-embed-button="article_entity_embed"] .card--small {
  max-width: 400px; }

.card--article {
  grid-row: span 2; }
  .card--article .card__copy {
    padding: 1rem;
    display: grid;
    grid-gap: 0px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "label label date" "title title title" "byline byline social"; }
  .card--article .card__title {
    grid-area: title;
    margin-top: 0.5rem;
    margin-bottom: 1.5rem; }
  .card--article .card__label {
    grid-area: label;
    font-family: var(--font-serif);
    font-size: var(--medium-font-size);
    color: var(--#000000);
    font-weight: var(--regular);
    line-height: var(--line-height); }
  .card--article .card__date {
    grid-area: date;
    text-align: right;
    font-size: var(--small-font-size); }
  .card--article .card__byline {
    grid-area: byline;
    font-size: var(--small-font-size); }
  .card--article.card--small {
    border: none;
    margin-bottom: 2rem; }
    .card--article.card--small .card__copy {
      padding: 0.5rem 0.5rem 1rem 0;
      display: grid;
      grid-gap: 0px;
      grid-template-columns: 1fr;
      grid-template-areas: "date" "title"; }
    .card--article.card--small .card__title {
      grid-area: title;
      margin: 0;
      font-family: var(--font-sans);
      font-size: var(--medium-font-size);
      color: var(--#000000);
      font-weight: var(--semi);
      line-height: 1.4em; }
    .card--article.card--small .label,
    .card--article.card--small .byline {
      display: none; }
    .card--article.card--small .date {
      grid-area: date;
      text-align: left;
      margin-bottom: 0.25rem; }
    .card--article.card--small img {
      border-radius: var(--border-radius); }

.card--feature {
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "top top" "label dateline" "byline social"; }
  .card--feature a {
    color: var(--#ffffff);
    margin: 1.5rem;
    padding: 1rem;
    text-align: center;
    border: 1px solid var(--#808080);
    font-weight: var(--semi);
    font-size: var(--font-size--huge);
    position: relative;
    z-index: 1; }
    .card--feature a:before {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: black;
      content: "";
      opacity: 0.5;
      z-index: -1; }
  .card--feature .card__image {
    grid-area: top; }
  .card--feature .card__title {
    grid-area: top;
    display: flex;
    align-items: flex-end; }
  .card--feature .card__label {
    grid-area: label;
    font-family: var(--font-serif);
    font-size: var(--base-font-size);
    color: var(--#000000);
    font-weight: var(--regular);
    line-height: var(--line-height);
    font-size: var(--medium-font-size);
    margin: 1rem 1rem; }
  .card--feature .card__date {
    grid-area: dateline;
    text-align: right;
    font-size: var(--small-font-size);
    margin: 20px 1rem; }
  .card--feature .card__byline {
    grid-area: byline;
    font-size: var(--small-font-size);
    margin: 0 1rem 1rem; }

.card--stat {
  padding: 1rem;
  text-align: center;
  grid-row: span 1; }
  .card--stat .card__title {
    display: none; }
  .card--stat .card__label {
    font-family: var(--font-sans);
    font-size: 2rem;
    color: var(--#000000);
    font-weight: var(--heavy);
    line-height: var(--line-height);
    border-bottom: solid 4px var(--#008000); }
  .card--stat .card__body {
    font-weight: var(--semi); }

.card--course {
  padding: 1rem; }

/* -------------------------

Title: Site header styles
Descr: Set 
        - #skip
        - header
        - .cu-logo
        - .as-logo
all search form design in _forms.scss

--------------------------- */
#skip {
  position: absolute;
  top: 50px;
  z-index: 3; }
  #skip a {
    z-index: 0;
    position: absolute;
    left: -5000px;
    width: 250px;
    font-weight: 600; }
    #skip a:focus {
      left: 200px; }

header {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: auto auto auto;
  grid-template-areas: "logo logo logo buttons" "nav nav nav nav" "search search search search";
  max-width: var(--xl);
  margin: 0 auto; }
  @media (min-width: 782px) {
    header {
      grid-template-columns: auto 1fr auto;
      grid-template-rows: auto auto; } }
  @media (min-width: 782px) {
    header {
      grid-template-areas: "logo nav buttons" "search search search"; } }
  header form {
    grid-area: search; }
  header .header__lockup {
    grid-area: logo;
    display: flex;
    justify-content: center;
    align-items: center; }
  header .header__mainNav {
    grid-area: nav;
    width: 100%; }
    @media (min-width: 782px) {
      header .header__mainNav {
        width: auto;
        display: flex;
        flex-direction: column;
        justify-content: center; } }
  header .main-nav-toggle {
    grid-area: navIcon; }
  header .img {
    max-width: initial; }
  @media (min-width: 782px) {
    header {
      flex-direction: row;
      padding: 3rem 1rem; } }

.cu-logo {
  text-indent: -9999px;
  width: 80px;
  height: 80px;
  background: url(/themes/custom/pathways/images/CU-insignia.svg);
  background-size: 80px 80px; }

.as-logo {
  text-indent: -9999px;
  width: 193px;
  height: 47px;
  background: url(/themes/custom/pathways/images/college.svg);
  background-size: 193px 47px;
  position: relative;
  margin-left: 20px; }
  .as-logo:before {
    position: absolute;
    width: 1px;
    background: var(--gray-lt);
    height: 100%;
    top: 0;
    left: 0px;
    content: ""; }

.header__buttons {
  grid-area: buttons;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header__buttons button {
    border: none;
    background: none;
    cursor: pointer; }
  @media (min-width: 782px) {
    .header__buttons .iconButton--showNav {
      display: none; } }
